@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
.Cart-Total-Value{
    width: 80%;
    margin: 0 auto;
    padding: 2rem;
    text-align: center;
    font-size: 1.5rem;
}
.Cart-Empty{
    /* width: 100%; */
    margin: 0 auto;
    text-align: center;
    padding: 2rem;
    font-size: 2rem;
    color: orangered;
}
.Cart-Empty p{
    color: black;
    opacity: 0.7;
}
.Cart-Total-Value span{
    color: red;
    font-weight: 600;
}
.Cart-Product-Card{
    max-width: 50%;
    height: 15rem;
    padding: 1rem;
    margin: 0 auto;
    display: -webkit-flex;
    display: flex;
    margin-top: 2rem;
    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.09), 0 10px 10px rgba(0, 0, 0, 0.09);
}

.Cart-Product-Card .Cart-Product-div{
    width: 50%  ;
}
.Cart-Product-Card .Cart-Product-Info{
    padding: 1rem;
}
.Cart-Product-Card .Cart-Product-Info p{
    font-size: 25px;
    font-weight: 600;
    color: black;
    margin-bottom: 1rem;
}

.Cart-Product-Card .Cart-Product-Info button{
    background-color: white;
    font-size: 20px;
    border-radius: 3px;
    margin-bottom: 1rem;
    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.09), 0 10px 10px rgba(0, 0, 0, 0.09);
}
.Cart-Product-Card .Cart-Product-Info button:hover{
    background-color: black;
    color: white;
    transition: 0.4s;
    cursor: pointer;
}
.Cart-Product-Card .Cart-Product-Info span{
    font-size: 22px;
    padding: 1rem;
}

.Cart-Product-Card .Cart-Product-Info h3{
    color: red;
    margin-bottom: 1rem;
}
.Filter-Wrap{
    width: auto;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.Filter-Wrap .clear-filter{
    color: black;
    cursor: pointer;
    padding: 1rem;

}
.main-wrapper{
    display: -webkit-flex;
    display: flex;
}

.main-wrapper .middle-main-div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}

.Card-main-div{
    width: 15rem;
    padding: 10px;
    border-radius: 0.2rem;
    margin: 1rem;
    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.09), 0 10px 10px rgba(0, 0, 0, 0.09);

}
.Card-main-div .img-div img{
    width: 100%;
    height: auto;
}
.Card-main-div .img-div .product-info{
    margin: 0.2rem;
}
.Card-main-div .img-div .product-info .material-name{
    font-size: 18px;
    font-weight: 500;
    opacity: 0.5;
}
.Card-main-div .img-div .product-info .product-price{
    color: black;
    font-weight: bold;
    margin-top: 0.7rem;
}

.Card-main-div .bottom-btn-div{
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    margin-top: 0.5rem;
}
.Card-main-div .bottom-btn-div button{
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 100%;
    padding: 10px;
    border: none;
    width: 50%;
    background-color: white;
}
.Card-main-div .bottom-btn-div button:hover{
    background-color: black;
    color: white;
    cursor: pointer;
    transition: 0.4s;
}
/* Fast Dilvery Satus */
.Card-main-div .img-div .product-info .FastDelivery-Tag-true{
    color: red;
    margin-top: 0.7rem;
}
.Card-main-div .img-div .product-info .FastDelivery-Tag-false{
    text-decoration: line-through;
    margin-top: 0.7rem;
}
/* In Stock Status */
.Card-main-div .img-div .product-info .inStock-Tag-False{
    font-weight: 600;
    color: #00fa9a;
}
.Card-main-div .img-div .product-info .inStock-Tag-True{
    font-weight: 500;
    text-decoration: line-through;
}
   
