body {
  font-family: "Open Sans", sans-serif;
}

* {
  margin: 0;
  border: 0;
}



.navbar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    background-color: white;
    border-bottom: 1px solid gray;
    padding: 1rem;
    color: black;
}
.navbar .Fashion-Name{
    width: 9rem;
}
.navbar div .navbar-brand{
    text-decoration: none;
    color: red;
}
.navbar div .navbar-brand span{
    color: black;
}

.navbar div{
    font-size: 18px;
    font-weight: 400;
}
.navbar div:hover{
    cursor: pointer;
    color: red;
}

.navbar .div-Cartbtn{
   /* background-color: red; */
   padding-left: 2rem;
   padding-right: 2rem;
}
.navbar .div-Cartbtn button{
    width: 7rem;
    height: 2rem;
    font-style: 18px;
    background-color: white;
    box-shadow: 0 10px 22px rgba(0, 0, 0, 0.09), 0 10px 10px rgba(0, 0, 0, 0.09);
}
.navbar .div-Cartbtn button:hover{
    background-color: black;
    color: white;
    transition: 0.5s;
    cursor: pointer;
}

.Home-main-div {
  width: 100%;
}
.Home-main-div .Home-img-div{
  height: 30rem;
  padding: 20px;
  margin: 0 auto;
  text-align: center;
}
.Home-main-div .Home-img-div img{
  margin: 0 auto;
  width: 90%;
  height: 100%;
}
.Home-main-div .Home-img-div button{
  width: 10rem;
  height: 3.5rem;
  border-radius: 5px;
  margin-right: 2%;
  font-size: 22px;
  box-shadow: 0 10px 22px rgba(0, 0, 0, 0.09), 0 10px 10px rgba(0, 0, 0, 0.09);
  background-color: white;
}
.Home-main-div .Home-img-div button:hover{
  background-color: black;
  color: white;
  cursor: pointer;
  transition: 0.4s;
}

